@import "../../../assets/style/app";

.showSidebarButtonWrapper {
  position: absolute!important;
  top: 0;
  left: 0;
  z-index: 222;
  background: rgba(0,0,0, 0.5);
  border-radius: 0 0 50% 0;

  @include desktop {
    display: none;
  }

  .icon {
    transition: 0.3s;

    &_show {
      transform: rotate(-180deg);
    }
  }
}
