@import "../../assets/style/app";

.app {
  position: relative;
  height: 100vh;
  width: 100vw;
  overflow: hidden;
  background: grey;

  .cloudLeft {
    transform: translateX(-50%);
  }

  .cloudRight {
    transform: translateX(50%);
  }

  .inner {
    z-index: 20;
    position: relative;
    height: 100%;
    @include inner;

    .main {
      display: flex;
      height: calc(100vh - 60px);
      background: #b0bec5;
    }
  }
}
