@import "../../../../assets/style/app";

.wrapper {
  @include button;
  flex-basis: 100%;

  .fileListItem {
    width: 100%;
    height: 100%;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    align-items: center;
    padding: 5px 20px;

    &:hover {
      background: #eee;
    }

    &_selected {
      background: #eee;
    }

    &_notTable {
      display: flex;
      flex-basis: 100px;
      align-items: flex-start;
      padding: 10px;
    }

    .leftPart {
      display: flex;
      flex-direction: row;
      align-items: center;

      &_noTable {
        flex-direction: column;
      }
    }

    .rightPart {
      display: grid;
      grid-template-columns: repeat(3, 1fr);
    }

  }
}


