.cloud1 {
  animation-name: rtl;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
}

.cloud2 {
  animation-name: ltr;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
}

@keyframes rtl {
  0% {
    transform: translateX(0%);
  }
  50% {
    transform: translateX(300%);
  }
  100% {
    transform: translateX(0%);
  }
}

@keyframes ltr {
  0% {
    transform: translateX(0%);
  }
  50% {
    transform: translateX(-300%);
  }
  100% {
    transform: translateX(0%);
  }
}

