@import "../../../assets/style/app";

.authBlock {
  @include centerColumn;
  padding: 16px;

  @include desktop {
    padding: 24px;
  }

  .description {
    margin-top: 8px;
  }

  .buttons {
    align-self: stretch;

    .button {
      margin-top: 16px;
      text-transform: none;
      border-radius: 999px;

      &:not(:first-child) {
        margin-top: 10px;
      }
    }
  }
}
