@import "../../assets/style/app";

.sidebarWrapper {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  z-index: 111;
  //width: 200px;

  @include desktop {
    width: 200px;
    flex-shrink: 0;
    flex-grow: 0;
  }

  .sidebar {
    position: absolute;
    padding-top: 36px;
    z-index: 111;
    top: 0;
    left: 0;
    bottom: 0;
    width: 200px;
    background: #546e7a;
    height: 100%;
    box-sizing: border-box;
    transform: translateX(-101%);
    transition: 0.3s;

    @include desktop {
      position: relative;
      padding-top: 0;
      transform: translateX(0);
    }

    &_showSidebar {
      transform: translateX(0);
    }
  }

  @include desktop {
    position: relative;
    padding-top: 0;

    .sidebar {
      position: relative;
    }
  }
}



