@import "../../assets/style/app";

.contextFileItemMenu {
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  z-index: 999;

  height: 60px;
  background: #222;
  padding: 0 20px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  transform: translateY(-101%);
  transition: 0.3s;

  &_show {
    transform: translateY(0);
  }

  p {
    color: #FFF;
  }

  .right {
    display: flex;
    align-items: center;

    .btn {
      margin-right: 20px;
    }
  }
}
