.filesCommonPage {
  position: relative;
  flex-grow: 1;
  padding: 10px;
  box-sizing: border-box;
  height: 100%;
  overflow: auto;

  .preloader {
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
  }

  .card {
    padding: 10px;
    box-sizing: border-box;
    height: 100%;
    position: relative;
  }
}
