@import "../../assets/style/app";

.header {
  height: 60px;
  background: #37474f;
  padding: 0 20px;
  display: flex;
  align-items: center;
  justify-content: space-between;

  .logo {
    display: flex;
    align-items: center;
    text-decoration: none;
    transition: 0.3s;

    p {
      margin-left: 10px;
      color: #FFF;
    }

    &:hover {
      text-shadow: 0 0 20px #FFF;
    }
  }

  .right {
    display: flex;
    align-items: center;
  }
}
